/* ===== HOMEPAGE LAYOUT ===== */
.homepage {
  flex-grow: 1;
  padding: clamp(1rem, 3vw, 2rem);
  background: linear-gradient(180deg, var(--dark-1) 0%, var(--dark-2) 100%);
  min-height: 100vh;
  position: relative;
  z-index: 0;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden; 
  contain: layout paint;
}

.homepage::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 30%, rgba(59, 130, 246, 0.15) 0%, transparent 40%),
    radial-gradient(circle at 80% 70%, rgba(99, 102, 241, 0.1) 0%, transparent 40%);
  pointer-events: none;
  z-index: -1;
}

.section-container {
  max-width: min(1200px, 90vw);
  margin: 0 auto;
  animation: fadeIn 0.6s ease-out forwards;
}

.section-title {
  color: var(--light-1);
  margin: clamp(1rem, 3vw, 1.5rem) 0;
  font-size: clamp(1.8rem, 5vw, 2.4rem);
  font-weight: 700;
  position: relative;
  display: inline-block;
  padding-bottom: 0.5rem;
}

.section-title::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: min(80px, 20vw);
  height: clamp(3px, 0.5vw, 4px);
  background: linear-gradient(to right, var(--primary), var(--secondary));
  border-radius: 4px;
}

.section-content {
  background: linear-gradient(145deg, var(--dark-2) 0%, var(--dark-3) 100%);
  padding: clamp(1.5rem, 3vw, 2rem);
  border-radius: clamp(8px, 2vw, 16px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: clamp(1.5rem, 3vw, 2rem);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.section-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
}

/* ===== EXPERIENCE TIMELINE ===== */
.experience-timeline {
  position: relative;
  padding-left: clamp(1rem, 3vw, 1.5rem);
}

.experience-timeline::before {
  content: '';
  position: absolute;
  left: calc(0.5rem + 0.5vw);
  top: 0;
  height: 100%;
  width: clamp(2px, 0.5vw, 3px);
  background: linear-gradient(to bottom, var(--primary), var(--secondary));
}

.experience-item {
  position: relative;
  margin-bottom: clamp(1.5rem, 3vw, 2rem);
  padding-left: clamp(1rem, 3vw, 1.5rem);
  transition: all 0.3s ease;
  background: linear-gradient(
    to right,
    rgba(30, 41, 59, 0.6) 0%,
    rgba(15, 23, 42, 0.6) 100%
  );
  backdrop-filter: blur(5px);
  border-radius: 8px;
  padding: 1.2rem;
  contain: layout;
}

.experience-item:hover {
  transform: translateY(-3px);
}

.experience-item::before {
  content: '';
  position: absolute;
  left: calc(-0.5rem - 0.5vw);
  top: 0.5rem;
  width: clamp(0.8rem, 2vw, 1rem);
  height: clamp(0.8rem, 2vw, 1rem);
  border-radius: 50%;
  background: var(--primary);
  border: 3px solid var(--dark-2);
  transition: all 0.3s ease;
}

.company {
  color: var(--light-1);
  font-weight: 600;
  font-size: clamp(1rem, 2vw, 1.2rem);
  margin: 0.25rem 0;
}

.duration, .grade {
  color: var(--light-3);
  font-size: clamp(0.8rem, 1.5vw, 0.9rem);
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.achievements {
  list-style-type: none;
  padding-left: 0;
}

.achievements li {
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  padding-left: clamp(1rem, 3vw, 1.5rem);
  position: relative;
  line-height: 1.6;
}

.achievements li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-size: 1.2em;
}

/* ===== SKILLS GRID ===== */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(250px, 100%), 1fr));
  gap: clamp(1rem, 2vw, 1.5rem);
}

.skill-category {
  transition: transform 0.3s ease;
  background: rgba(30, 41, 59, 0.6);
  backdrop-filter: blur(5px);
  transition: 
    transform var(--transition-normal),
    box-shadow var(--transition-normal);
  padding: 1.2rem;
  border-radius: 8px;
}

.skill-category:hover {
  transform: translateY(-3px);
  box-shadow: 
    0 10px 30px rgba(0, 0, 0, 0.3),
    inset 0 0 15px rgba(59, 130, 246, 0.2);
}

.skill-category h3 {
  color: var(--light-1);
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  font-size: clamp(1rem, 2vw, 1.1rem);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skill-category ul {
  list-style-type: none;
  padding-left: 0;
}

.skill-category li::before {
  content: '•';
  color: var(--primary);
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

/* ===== EDUCATION ===== */
.education-list {
  display: flex;
  flex-direction: column;
  gap: clamp(1.5rem, 3vw, 2rem);
}

.education-item {
  background: rgba(30, 41, 59, 0.4);
  padding: clamp(1rem, 2vw, 1.5rem);
  border-radius: clamp(8px, 2vw, 12px);
  border-left: clamp(3px, 1vw, 4px) solid var(--primary);
  transition: all 0.3s ease;
}

.education-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  background: rgba(30, 41, 59, 0.6);
}

.institution {
  font-weight: 600;
  color: var(--light-1);
  font-size: clamp(1rem, 2vw, 1.2rem);
  margin: clamp(0.3rem, 1vw, 0.5rem) 0;
}

.skills {
  margin-top: clamp(0.8rem, 2vw, 1rem);
  padding-top: clamp(0.8rem, 2vw, 1rem);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.skills h4 {
  font-size: clamp(0.9rem, 1.8vw, 1rem);
  margin-bottom: clamp(0.3rem, 1vw, 0.5rem);
  color: var(--light-2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skills ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: clamp(0.3rem, 1vw, 0.5rem);
}

.skills li {
  background: rgba(59, 130, 246, 0.2);
  padding: clamp(0.2rem, 1vw, 0.3rem) clamp(0.5rem, 2vw, 0.8rem);
  border-radius: 20px;
  font-size: clamp(0.75rem, 1.5vw, 0.85rem);
  transition: all 0.2s ease;
}

/* ===== ANIMATIONS ===== */
@keyframes pulse {
  0% { opacity: 0.5; transform: scale(0.95); }
  100% { opacity: 0.8; transform: scale(1.05); }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ===== RESPONSIVE FIXES ===== */
@media (max-width: 1024px) {
  .main-content {
    padding-left: 0;
  }
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }

  .experience-timeline::before {
    left: 0.35rem;
  }

  .experience-item::before {
    left: -0.35rem;
  }
}

@media (max-width: 480px) {
  .homepage {
    padding: 1rem;
  }
  
  .section-content {
    padding: 1.2rem;
  }
  
  .achievements li {
    padding-left: 1.2rem;
  }
}