/* ===== SIDEBAR CONTAINER ===== */
.sidebar-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: var(--sidebar-width);
    z-index: 25;
    box-shadow: var(--shadow-lg);
    transform: translateX(0);
    transition: transform var(--transition-slow);
    contain: strict;
  }
  
  .sidebar {
    width: 100%;
    height: 100vh;
    background: linear-gradient(
      145deg, 
      rgba(30, 41, 59, 0.95) 0%, 
      rgba(15, 23, 42, 0.95) 100%
    );
    padding: clamp(1.5rem, 3vw, 2rem);
    position: relative;
    overflow-y: auto;
    overflow-x: hidden;
    backdrop-filter: blur(10px);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    transition: transform var(--transition-slow);
    contain: strict;
  }
  
  .sidebar-inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: clamp(1.5rem, 3vw, 2rem);
    contain: layout;
  }
  
  /* ===== PROFILE CARD ===== */
  .profile-card {
    background: linear-gradient(
      45deg,
      rgba(59, 130, 246, 0.1) 0%,
      rgba(30, 41, 59, 0.5) 100%
    );
    border-radius: clamp(12px, 2vw, 16px);
    padding: clamp(1rem, 2vw, 1.5rem);
    border: 1px solid rgba(255, 255, 255, 0.1);
    transition: transform var(--transition-normal);
    position: relative;
    overflow: hidden;
    contain: layout;
  }
  
  .avatar-container {
    width: clamp(100px, 20vw, 120px);
    height: clamp(100px, 20vw, 120px);
    margin: 0 auto clamp(1rem, 2vw, 1.5rem);
    position: relative;
    contain: layout;
  }
  
  .avatar {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid rgba(59, 130, 246, 0.3);
    box-shadow: 
      0 0 30px rgba(59, 130, 246, 0.2),
      inset 0 0 20px rgba(59, 130, 246, 0.1);
    transition: all var(--transition-normal);
  }
  
  /* ===== PROFILE INFO ===== */
  .profile-info h1 {
    font-size: clamp(1.3rem, 3vw, 1.5rem);
    text-align: center;
    margin-bottom: clamp(0.2rem, 0.5vw, 0.25rem);
    color: var(--light-1);
  }
  
  .title {
    text-align: center;
    color: var(--light-3);
    margin-bottom: clamp(0.8rem, 2vw, 1rem);
    font-size: clamp(0.9rem, 2vw, 1rem);
    position: relative;
  }
  
  .title::after {
    content: '';
    display: block;
    width: 50px;
    height: 2px;
    background: linear-gradient(to right, var(--primary), transparent);
    margin: 0.5rem auto 0;
  }
  
  /* ===== CONTACT INFO ===== */
  .contact-info {
    display: flex;
    flex-direction: column;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    margin: clamp(1rem, 2vw, 1.5rem) 0;
    contain: layout;
  }
  
  .contact-item {
    display: flex;
    align-items: center;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    font-size: clamp(0.8rem, 1.8vw, 0.9rem);
    transition: transform var(--transition-fast);
  }
  
  .contact-icon {
    width: clamp(28px, 5vw, 30px);
    height: clamp(28px, 5vw, 30px);
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 50%;
    transition: all var(--transition-normal);
  }
  
  /* ===== SIDEBAR NAV ===== */
  .sidebar-nav {
    display: flex;
    flex-direction: column;
    gap: clamp(0.4rem, 1vw, 0.5rem);
    contain: layout;
  }
  
  .nav-item {
    padding: clamp(0.6rem, 1.5vw, 0.75rem) clamp(0.8rem, 2vw, 1rem);
    border-radius: clamp(6px, 1.5vw, 8px);
    display: flex;
    align-items: center;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    background: rgba(30, 41, 59, 0.6);
    transition: all var(--transition-normal);
    font-size: clamp(0.85rem, 2vw, 1rem);
    backdrop-filter: blur(5px);
    border: none;
    cursor: pointer;
    color: var(--light-2);
    text-align: left;
  }
  
  .nav-item.active {
    background: linear-gradient(90deg, rgba(59, 130, 246, 0.8), rgba(99, 102, 241, 0.7));
  }
  
  /* ===== TECH STACK ===== */
  .tech-tags {
    display: flex;
    flex-wrap: wrap;
    gap: clamp(0.3rem, 1vw, 0.5rem);
    contain: layout;
  }
  
  .tech-tag {
    padding: clamp(0.3rem, 1vw, 0.4rem) clamp(0.6rem, 1.5vw, 0.8rem);
    border-radius: 20px;
    font-size: clamp(0.75rem, 1.5vw, 0.8rem);
    background: rgba(255, 255, 255, 0.08);
    transition: all var(--transition-fast);
  }
  
  /* ===== MOBILE RESPONSIVE ===== */
  @media (max-width: 1024px) {
    .sidebar-container {
      transform: translateX(-100%);
      width: 80%;
      max-width: 300px;
      z-index: 30;
    }
    
    .sidebar-container.active {
      transform: translateX(0);
    }
    
    /* Overlay when sidebar is active */
    .sidebar-overlay {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      backdrop-filter: blur(5px);
      z-index: 20;
      opacity: 0;
      pointer-events: none;
      transition: opacity var(--transition-slow);
    }
    
    .sidebar-container.active + .sidebar-overlay {
      opacity: 1;
      pointer-events: all;
    }
  }
  
  @media (max-width: 768px) {
    .sidebar {
      padding: 1.2rem;
    }
    
    .sidebar-inner {
      gap: 1.2rem;
    }
    
    .nav-item {
      padding: 0.8rem;
    }
  }