/* ===== BASE STYLES & VARIABLES ===== */
:root {
  --dark-1: #0f172a;
  --dark-2: #1e293b;
  --dark-3: #334155;
  --light-1: #f8fafc;
  --light-2: #e2e8f0;
  --light-3: #94a3b8;
  --primary: #3b82f6;
  --secondary: #6366f1;
  --sidebar-width: clamp(280px, 25vw, 330px);
  --transition-fast: 0.2s ease;
  --transition-normal: 0.3s ease;
  --transition-slow: 0.4s cubic-bezier(0.22, 1, 0.36, 1);
  --shadow-sm: 0 5px 15px rgba(0, 0, 0, 0.2);
  --shadow-md: 0 10px 30px rgba(0, 0, 0, 0.3);
  --shadow-lg: 0 15px 35px rgba(0, 0, 0, 0.4);
  --perspective: 2000px;
  --depth-1: 20px;
  --depth-2: 40px;
  --depth-3: 60px;
  --glow: radial-gradient(circle at 50% 50%, 
    rgba(59, 130, 246, 0.4), 
    rgba(99, 102, 241, 0.2), 
    transparent 70%
  );
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: clamp(14px, 1.5vw, 16px);
  overflow-x: hidden;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  line-height: 1.6;
  color: var(--light-2);
  background-color: var(--dark-1);
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  overflow-x: hidden;
  width: 100%;
  position: relative;
}

/* ===== MAIN LAYOUT ===== */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
  contain: layout paint;
}

.layout-grid {
  display: flex;
  flex: 1;
  position: relative;
}

/* ===== SIDEBAR STYLES ===== */
.sidebar-container {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: var(--sidebar-width);
  z-index: 25;
  box-shadow: var(--shadow-lg);
  transform: translateX(0);
  transition: transform var(--transition-slow);
}

/* ===== MAIN CONTENT ===== */
.main-content {
  flex: 1;
  position: relative;
  z-index: 10;
  padding-left: var(--sidebar-width);
  transition: 
    padding var(--transition-slow),
    transform var(--transition-slow);
  width: 100%;
  min-height: 100vh;
  contain: layout paint;
}

/* ===== MOBILE STYLES ===== */
.mobile-header {
  display: none;
  align-items: center;
  justify-content: space-between;
  padding: clamp(0.8rem, 2vw, 1rem);
  background: var(--dark-2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  z-index: 30;
  position: sticky;
  top: 0;
  backdrop-filter: blur(8px);
  width: 100%;
}

.menu-toggle {
  background: none;
  border: none;
  color: var(--light-2);
  font-size: clamp(1.2rem, 3vw, 1.5rem);
  cursor: pointer;
  transition: transform var(--transition-normal);
  padding: 0.5rem;
  border-radius: 50%;
}

.menu-toggle:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.1);
}

.mobile-title {
  font-size: clamp(1rem, 3vw, 1.2rem);
  font-weight: 600;
  margin-left: clamp(0.5rem, 2vw, 1rem);
  background: linear-gradient(90deg, var(--light-1), var(--light-3));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  white-space: nowrap;
}

/* ===== OVERLAY ===== */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  z-index: 20;
}

/* ===== RESPONSIVE STYLES ===== */
@media (max-width: 1024px) {
  .mobile-header {
    display: flex;
  }

  .sidebar-container {
    transform: translateX(-100%);
  }

  .sidebar-container.active {
    transform: translateX(0);
  }

  .main-content {
    padding-left: 0;
  }
  
  .app-container.sidebar-open {
    overflow: hidden;
    height: 100vh;
  }
}

@media (max-width: 768px) {
  .sidebar-container {
    width: 80%;
    max-width: 300px;
  }
}

/* ===== BACKGROUND ELEMENTS ===== */
.background-elements {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
  overflow: hidden;
}

.tech-circle {
  position: absolute;
  width: min(600px, 80vw);
  height: min(600px, 80vw);
  right: max(-200px, -20vw);
  bottom: max(-200px, -20vw);
  background: radial-gradient(circle, rgba(59, 130, 246, 0.1) 0%, transparent 70%);
  border-radius: 50%;
  filter: blur(clamp(20px, 5vw, 40px));
  opacity: 0.8;
  animation: pulse 15s ease-in-out infinite alternate;
}

.binary-grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(255, 255, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
  background-size: clamp(30px, 5vw, 40px) clamp(30px, 5vw, 40px);
  opacity: 0.5;
}

.network-lines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(to right, 
      transparent calc(var(--sidebar-width) - 1px), 
      rgba(255, 255, 255, 0.05) var(--sidebar-width), 
      transparent calc(var(--sidebar-width) + 1px));
  transition: background-image var(--transition-normal);
}

/* ===== ANIMATIONS ===== */
@keyframes pulse {
  0% { opacity: 0.5; transform: scale(0.95); }
  100% { opacity: 0.8; transform: scale(1.05); }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ===== SCROLLBAR STYLING ===== */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(30, 41, 59, 0.3);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, var(--primary), var(--secondary));
  border-radius: 10px;
}

/* ===== CRITICAL MOBILE FIXES ===== */
@media (max-width: 1024px) {
  .background-elements {
    display: none;
  }
  
  .network-lines {
    background-image: none;
  }
}

@media (max-width: 768px) {
  .tech-circle {
    right: -30vw;
    bottom: -30vw;
  }
}

/* Text wrapping insurance */
h1, h2, h3, h4, h5, h6,
p, li, a, span {
  word-wrap: break-word;
  overflow-wrap: break-word;
}

/* Image containment */
img, svg, video {
  max-width: 100%;
  height: auto;
  display: block;
}